.port .container{
    width: auto;
    height: 100%;
    display: flex;
    float: left;
    justify-content: center;
    margin-bottom: 100px;
}

.port .container .right{
    flex: 50%;
    width: 50%;
    height: 100%;
    display: flex;
    float: left;
    justify-content: center;
}

.port .container .left{
    flex: 50%;
    width: 50%;
    height: 100%;
    float: right;
    align-items: initial;
    
}

.port .container .left button{
    background-color: azure;
    border-radius: 50px;
    font-size: 18px;
    padding: 15px 30px;
}
.port .container .left h1,h3{
    color: #96d5d2;
    max-width: 100% ;
    margin-top:100px ;
}


.port .right img{
    width: 60%;
    height: 100%;
    float:right;
    margin-top: 100px;
    border-radius: 100%;
}

